.navbar {
    background-color: #007BFF; 
    padding: 1rem;
}

.navbar a {
    color: white;
    text-decoration: none;
    margin: 0 15px;
    font-weight: bold;
    font-family: 'Comic Sans MS', cursive, sans-serif; 
}

.navbar a:hover {
    text-decoration: underline;
    color: #FFB6C1; 
}
